.clothessection{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-top: 47px;
}

.clothessection__wrapper{
    display: flex;
    height: 25px;
    box-sizing: border-box;
}

.clothessection__title{
    font-size: 20px;
    margin: 0;
    box-sizing: border-box;
    height: 25px;
    width: 119px;
}

.clothessection__button{
    border: none;
    font-family: 'Cabinet Grotesk';
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
    background: none;
    color: grey;
    box-sizing: border-box;
    height: 25px;
    transition: 0.1s ease-in-out;
}

.clothessection__cards{
    grid-template-columns: repeat(3, 328px);
    max-width: 1015px;
    justify-content: center;
}

.clothessection__button:hover{
    color: black;
}